import { useState } from "react";
import { Form } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import Logos from "./Logos";
const Login = () => {
  const [validated, setValidated] = useState(false);
  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
      setValidated(true);
      // console.log("not submit the form")
    } else {
      event.preventDefault();
      window.location.pathname = "/home";
      setValidated(true);
      // console.log("submitted the form")
    }
  };

  return (
   <>
    <Logos />
    <div className="card">
    <div className="tittle">
      <h3>     </h3>
      <h3>   </h3>
    </div>

    <Form noValidate validated={validated} onSubmit={handleSubmit} className="login">
      <Form.Group className="mb-3 right" controlId="formBasicEmail">
        <Form.Label> </Form.Label>
        <Form.Control
          type="text"
          placeholder="   "
          required
        />
        <Form.Control.Feedback type="invalid">
               
        </Form.Control.Feedback>
      </Form.Group>
      <Form.Group className="mb-3 right" controlId="formBasicEmail">
        <Form.Label>  </Form.Label>
        <Form.Control
          type="number"
          placeholder="    "
          required
        />
        <Form.Control.Feedback type="invalid">
               
        </Form.Control.Feedback>
      </Form.Group>

      <Form.Group className="mb-3 right" controlId="formBasicPassword">
        <Form.Label> </Form.Label>
        <Form.Control type="number" placeholder=" " required />
        <Form.Control.Feedback type="invalid">
               
        </Form.Control.Feedback>
      </Form.Group>
      <Button className="btn" type="submit">
        
      </Button>
      <Link to="/register">
        <p>  </p>
      </Link>
    </Form>
  </div>
   </>
  );
};
export default Login;
